import React from 'react';
import T from 'prop-types';
import { graphql } from 'gatsby';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import styled, { css } from 'styled-components';
import nl2br from 'react-nl2br';
import Fade from 'react-reveal/Fade';
import { themeVal, glsp } from '@theme/utils';
import { media } from '@theme/media';
import { listReset, truncated } from '@theme/helpers';

import Layout from '../components/layout';
import CommunitySection from '../components/community-section';
import { FoldContent, FoldInner } from '../styles/fold';
import { Heading } from '../styles/heading';
import { Lead } from '../styles/type';
import { ArrowDecor } from '../styles/arrow-deco';
import withReveal from '../utils/with-reveal';

import aboutTpGif from '../images/about-tp.gif';

const HeroFold = styled.div`
  background: ${themeVal('color.base')};
  color: ${themeVal('color.surface')};
  /* Account for header height */
  padding: ${glsp(7.25, 2, 3, 2)};
  margin-top: ${glsp(-7.25)};
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;

  ${FoldContent} {
    display: flex;
    flex-flow: column;
    flex: 1;
    align-items: center;
    gap: ${glsp(2)};
  }

  ${Lead} {
    max-width: 44rem;
    text-align: center;
  }
`;

const HeroMedia = styled.div`
  aspect-ratio: 16/9;
  width: 100%;
  max-width: ${themeVal('layout.max')};
  border-radius: ${themeVal('shape.rounded2')};
  overflow: hidden;
`;

const AboutFoldHeader = styled.header`
  background: ${themeVal('color.neut4-200')};

  > ${FoldInner}:first-child {
    position: relative;
    padding-top: ${glsp(3)};
    padding-bottom: ${glsp(3)};

    ${media.largeUp`
      padding-top: ${glsp(5)};
      padding-bottom: ${glsp(4)};
    `}
  }

  ${FoldContent} {
    grid-column: auto / span 4;

    ${media.mediumUp`
      grid-column: auto / span 3;
    `}

    ${media.largeUp`
      grid-column: auto / span 6;
    `}
  }

  ${ArrowDecor} {
    position: absolute;
    bottom: 0;
    left: ${glsp()};
    transform: translate(0, 47%);
    color: ${themeVal('color.neut4-200')};

    ${media.mediumUp`
      left: ${glsp(2)};
    `}
  }
`;

const AboutFoldMedia = styled(FoldContent)`
  display: flex;
  justify-content: center;

  ${media.largeUp`
    justify-content:  end;
    margin: -2rem 0;
  `}
`;

const CardsFold = styled.div`
  ${({ variation }) => css`
    background: ${themeVal(['color', variation])};
  `}

  ${FoldContent} {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: start;
    padding: ${glsp(4, 0)};
    gap: ${glsp(2)};
    grid-column: 1 / -1;

    ${media.largeUp`
      padding: ${glsp(6, 0)};
    `}
  }
`;

const CardsList = styled.ul`
  ${listReset()}
  display: grid;
  width: 100%;
  grid-template-columns: repeat(4, 1fr);
  gap: ${glsp(1.5)};

  ${media.mediumUp`
    gap: ${glsp(2)};
    grid-template-columns: repeat(6, 1fr);
  `}

  ${media.largeUp`
    grid-template-columns: repeat(12, 1fr);
  `}

  li {
    display: flex;
    min-width: 0;
  }
`;

const CardLayoutBase = styled.div`
  display: flex;
  flex: 1;
  flex-flow: column;
  align-items: center;
  min-width: 0;
  gap: ${glsp(2)};
  background: ${themeVal('color.neut1')};
  padding: ${glsp(2)};

  ${Heading} {
    text-transform: none;
  }
`;

const TeamCardsList = withReveal(
  styled(CardsList)`
    li {
      grid-column: auto / span 4;

      ${media.smallUp`
      grid-column: auto / span 2;
    `}

      ${media.mediumUp`
      grid-column: auto / span 3;
    `}

    ${media.largeUp`
      grid-column: auto / span 4;
    `}
    }
  `,
  <Fade bottom cascade fraction={0.05} />
);

const TeamCard = styled(CardLayoutBase)`
  border-radius: ${themeVal('shape.rounded3')};
  background: ${themeVal('color.base')};
  padding: ${glsp(2, 0.75, 0.75, 0.75)};

  .gatsby-image-wrapper {
    max-width: 13rem;
  }
`;

const TeamCardContent = styled.div`
  flex: 1;
  background: ${themeVal('color.neut1')};
  border-radius: ${themeVal('shape.rounded3')} ${themeVal('shape.rounded3')}
    ${themeVal('shape.rounded2')} ${themeVal('shape.rounded2')};
  padding: ${glsp(2)};
  color: ${themeVal('color.neut3')};
`;

const TeamCardDescription = styled.p`
  word-break: break-word;
`;

const ModsFoldHeadline = styled.div`
  display: flex;
  flex-flow: column;
  max-width: 30rem;
  gap: ${glsp()};
`;

const ModsCardsList = withReveal(
  styled(CardsList)`
    li {
      grid-column: auto / span 4;

      ${media.smallUp`
      grid-column: auto / span 2;
    `}

      ${media.mediumUp`
      grid-column: auto / span 2;
    `}

    ${media.largeUp`
      grid-column: auto / span 4;
    `}

    ${media.xlargeUp`
      grid-column: auto / span 3;
    `}
    }
  `,
  <Fade bottom cascade />
);

const ModsCard = styled(CardLayoutBase)`
  border-radius: ${themeVal('shape.rounded2')};
  background: ${themeVal('color.neut1')};
  padding: ${glsp()};

  ${media.largeUp`
    padding: ${glsp(2)};
  `}

  ${Heading} {
    ${truncated()}
    max-width: 100%;
  }
`;

const IndexPage = (props) => {
  const { data } = props;

  return (
    <Layout>
      <main>
        <section>
          <HeroFold>
            <FoldContent>
              <HeroMedia>
                <iframe
                  width='100%'
                  height='100%'
                  src='https://www.youtube-nocookie.com/embed/QkKGlkbiOYI?controls=0'
                  title='YouTube video player'
                  frameBorder='0'
                  allow='accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture'
                  allowFullScreen
                />
              </HeroMedia>
              <Fade bottom>
                <Lead>
                  Thug Pugs is a metaverse brand made at the intersection of
                  outstanding NFT art and compelling story to fuel a ruthless
                  game with juicy <strong>$TBONES</strong> for grabs.
                </Lead>
              </Fade>
            </FoldContent>
          </HeroFold>
        </section>

        <section>
          <AboutFoldHeader>
            <FoldInner>
              <FoldContent>
                <Fade bottom>
                  <Heading as='h1' size='xlarge'>
                    About
                  </Heading>
                  <Lead>
                    Pugs seemed doomed to live as fancy props â€” mere portable
                    delights for their human masters. But a few rogue ones
                    didn&apos;t want to play the nice-dog game anymore.
                    <br />
                    They are the Thug Pugs, and although they all seem adorable
                    to the eye, their territorial nature quickly emerged to make
                    things ugly, and gangs soon emerged.
                  </Lead>
                </Fade>
              </FoldContent>
              <AboutFoldMedia>
                <img
                  src={aboutTpGif}
                  alt='Thug Pugs logotype'
                  width={300}
                  height={317}
                />
              </AboutFoldMedia>
              <ArrowDecor />
            </FoldInner>
          </AboutFoldHeader>

          <CardsFold variation='neut1'>
            <FoldInner>
              <FoldContent>
                <Fade bottom>
                  <Heading as='h2' size='xlarge' variation='surface'>
                    The Team
                  </Heading>
                </Fade>
                <TeamCardsList>
                  {data.allTeamYaml.nodes.map((m) => (
                    <li key={m.id}>
                      <TeamCard>
                        <GatsbyImage
                          image={getImage(m.image)}
                          alt={`${m.name} avatar Thug Pug`}
                        />
                        <TeamCardContent>
                          <Heading as='h3' size='medium' variation='surface'>
                            {m.name}
                          </Heading>
                          <TeamCardDescription>
                            {nl2br(m.description)}
                          </TeamCardDescription>
                        </TeamCardContent>
                      </TeamCard>
                    </li>
                  ))}
                </TeamCardsList>
              </FoldContent>
            </FoldInner>
          </CardsFold>

          <CardsFold variation='base'>
            <FoldInner>
              <FoldContent>
                <ModsFoldHeadline>
                  <Fade bottom>
                    <Heading as='h2' size='xlarge' variation='surface'>
                      The Mods
                    </Heading>
                    <Lead variation='neut3'>
                      Our mods are awesome humans who help out the other amazing
                      humans in our community.
                    </Lead>
                  </Fade>
                </ModsFoldHeadline>
                <ModsCardsList>
                  {data.allModsYaml.nodes.map((m) => (
                    <li key={m.id}>
                      <ModsCard>
                        <GatsbyImage
                          image={getImage(m.image)}
                          alt={`${m.name} avatar Thug Pug`}
                        />
                        <Heading as='h3' size='medium' variation='surface'>
                          {m.name}
                        </Heading>
                      </ModsCard>
                    </li>
                  ))}
                </ModsCardsList>
              </FoldContent>
            </FoldInner>
          </CardsFold>
        </section>

        <CommunitySection />
      </main>
    </Layout>
  );
};

export default IndexPage;

IndexPage.propTypes = {
  data: T.shape({
    allTeamYaml: T.object,
    allModsYaml: T.object
  })
};

export const query = graphql`
  query {
    allTeamYaml {
      nodes {
        name
        id
        description
        image {
          childImageSharp {
            gatsbyImageData(width: 440, placeholder: BLURRED)
          }
        }
      }
    }

    allModsYaml {
      nodes {
        name
        id
        image {
          childImageSharp {
            gatsbyImageData(width: 180, placeholder: BLURRED)
          }
        }
      }
    }
  }
`;
